"use client";

import { useEffect, useMemo, useState } from "react";

export function FloatingRobotBot() {
  const [isOpen, setIsOpen] = useState(false);
  const [cursorTilt, setCursorTilt] = useState({ x: 0, y: 0 });
  const [theme, setTheme] = useState<"dark" | "light">(() => {
    if (typeof window === "undefined") {
      return "dark";
    }

    const savedTheme = window.localStorage.getItem("dashboard-theme");
    return savedTheme === "light" ? "light" : "dark";
  });

  useEffect(() => {
    if (typeof window === "undefined") {
      return;
    }

    const onStorage = () => {
      const savedTheme = window.localStorage.getItem("dashboard-theme");
      if (savedTheme === "dark" || savedTheme === "light") {
        setTheme(savedTheme);
      }
    };

    window.addEventListener("storage", onStorage);
    const intervalId = window.setInterval(onStorage, 1000);

    return () => {
      window.removeEventListener("storage", onStorage);
      window.clearInterval(intervalId);
    };
  }, []);

  useEffect(() => {
    if (typeof window === "undefined") {
      return;
    }

    const handleMouseMove = (event: MouseEvent) => {
      const centerX = window.innerWidth / 2;
      const centerY = window.innerHeight / 2;
      const normalizedX = (event.clientX - centerX) / centerX;
      const normalizedY = (event.clientY - centerY) / centerY;

      setCursorTilt({
        x: Math.max(-1, Math.min(1, normalizedX)),
        y: Math.max(-1, Math.min(1, normalizedY)),
      });
    };

    window.addEventListener("mousemove", handleMouseMove);
    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
    };
  }, []);

  const bubbleClass =
    theme === "dark"
      ? "bg-zinc-800/95 text-zinc-100"
      : "bg-white text-zinc-900";

  const robotTransform = useMemo(
    () => ({
      transform: `perspective(700px) rotateX(${cursorTilt.y * -10}deg) rotateY(${cursorTilt.x * 14}deg) translateZ(0)`,
    }),
    [cursorTilt.x, cursorTilt.y],
  );

  const eyesTransform = useMemo(
    () => ({
      transform: `translate(${cursorTilt.x * 1.8}px, ${cursorTilt.y * 1.8}px)`,
    }),
    [cursorTilt.x, cursorTilt.y],
  );

  return (
    <div className="pointer-events-none fixed bottom-4 right-4 z-50 flex max-w-[220px] flex-col items-end gap-2 sm:bottom-5 sm:right-5">
      {isOpen ? (
        <div className={`pointer-events-auto rounded-xl px-3 py-2 text-xs shadow-lg ${bubbleClass}`}>
          <p className="font-medium">Asistente</p>
          <p className="mt-1 opacity-90">Hola, estoy aqu√≠ para ayudarte.</p>
        </div>
      ) : null}

      <button
        type="button"
        onClick={() => setIsOpen((current) => !current)}
        aria-label="Abrir asistente"
        className="pointer-events-auto relative h-16 w-16 rounded-2xl p-0.5 shadow-[0_16px_30px_-14px_rgba(14,165,233,0.9)] transition duration-150 hover:scale-[1.03] active:scale-95"
        style={robotTransform}
      >
        <span className="absolute -top-1 -right-1 h-3 w-3 rounded-full bg-cyan-400 shadow-[0_0_12px_rgba(34,211,238,0.95)]" />

        <span className="relative block h-full w-full rounded-[1rem] bg-gradient-to-br from-sky-400 via-blue-500 to-indigo-600 shadow-[inset_0_8px_16px_rgba(255,255,255,0.22)]">
          <span className="absolute inset-x-2 top-1.5 h-4 rounded-full bg-cyan-300/35 blur-[1px]" />
          <span className="absolute left-2 right-2 top-2 h-5 rounded-full bg-blue-950/90" />

          <span className="absolute left-[12px] top-[11px] h-2.5 w-2 rounded-full bg-cyan-300 shadow-[0_0_9px_rgba(34,211,238,0.95)] transition-transform duration-150" style={eyesTransform} />
          <span className="absolute right-[12px] top-[11px] h-2.5 w-2 rounded-full bg-cyan-300 shadow-[0_0_9px_rgba(34,211,238,0.95)] transition-transform duration-150" style={eyesTransform} />

          <span className="absolute -left-1 top-6 h-3 w-3 rounded-full bg-blue-400/80 shadow-[0_0_8px_rgba(59,130,246,0.7)]" />
          <span className="absolute -right-1 top-6 h-3 w-3 rounded-full bg-blue-400/80 shadow-[0_0_8px_rgba(59,130,246,0.7)]" />

          <span className="absolute bottom-2 left-3 h-2 w-1.5 animate-pulse rounded-full bg-cyan-300" />
          <span className="absolute bottom-2 right-3 h-2 w-1.5 animate-pulse rounded-full bg-cyan-300" />
        </span>
      </button>
    </div>
  );
}